body {
  justify-content: center;
  display: flex;
}

.people {
  cursor: default;
  grid-template-columns: repeat(4, 220px);
  justify-content: center;
  align-items: center;
  column-gap: 10px;
  width: 890px;
  display: grid;
}

.people__person {
  flex-direction: column;
  justify-content: center;
  gap: 8px;
  width: 200px;
  padding: 10px;
  transition: scale 1s;
  display: flex;
}

.people__person:hover {
  z-index: 1;
  background-color: #eee;
  border-radius: 10px;
  scale: 1.2;
}

.people__person:hover img {
  border-radius: 60px;
}

.people__person:hover .people__extra {
  opacity: 1;
}

.people__person:hover .people__pay:before {
  content: "$";
}

.people__person:hover .people__pay:after {
  content: "M";
}

.people__person:hover .people__sport:after {
  content: ",";
}

.people__details {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.people__photo {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  position: relative;
}

.people__name {
  letter-spacing: 0%;
  text-align: center;
  color: #2f4f4f;
  font-family: Arial;
  font-size: 20px;
  font-weight: 700;
  line-height: 100%;
}

.people__rank {
  letter-spacing: 0%;
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-family: Arial;
  font-size: 16px;
  font-weight: 700;
  line-height: 100%;
  display: flex;
  position: absolute;
  top: 88px;
  right: 10px;
}

.people__extra {
  opacity: 0;
  letter-spacing: 0%;
  text-align: center;
  margin-top: 8px;
  font-family: Arial;
  font-size: 18px;
  font-weight: 400;
  line-height: 100%;
}

img {
  object-fit: cover;
  border-radius: 50%;
  width: 200px;
  height: 200px;
  transition: border-radius 1s;
}

@media (width <= 1000px) {
  .people {
    grid-template-columns: repeat(2, 200px);
    column-gap: 30px;
  }
}
